@import url(https://fonts.googleapis.com/css?family=VT323);
@import url(https://fonts.googleapis.com/css?family=VT323);
.desktop {
	position: fixed;
	width: 100vw;
	height: 100vh;
	background-color: #272727;
}

.desktop-background {
	position: fixed;
	white-space: pre;
    font-family: 'VT323';
    font-size: 16pt;
	color: #8ec25b;
	-webkit-filter: blur(1.5px);
	-moz-filter: blur(1.5px);
	-o-filter: blur(1.5px);
	-ms-filter: blur(1.5px);
}
.window {
    font-family: 'VT323';
    font-size: 15pt;

	background-color: #222222;
	position: fixed;
	border: 1px solid transparent;
	border-radius: 2px;

	min-width: 300px;
	width: 70%;
	max-width: 700px;

	min-height: 300px;
	height: 70%;
	max-width: 700px;
}

.window-bar {
	background-color: #3b3b3b;
	border-top-left-radius: 3px;
	border-top-right-radius: 3px;
	width: calc(100% - 30px);
	height: calc(15pt);
	padding: 10px 15px;
	color: cyan;

	-webkit-user-select: none;  /* Chrome 49+ */
	-moz-user-select: none;     /* Firefox 43+ */
	-ms-user-select: none;      /* No support yet */
	user-select: none;
}

.window-bar-dots {
	float: left;
	height: 100%;
	padding-top: 6px;
}

.window-bar-dot {
	float: left;
	margin-right: 5px;
	position: relative;
	height: 10px;
	width: 10px;
	border-radius: 50%;

	opacity: 1;
	-webkit-transition: .1s;
	transition: .1s;
}

.window-bar-dots:hover .window-bar-dot {
	opacity: .8;
}

.window-bar-text {
	float: left;
	width: calc(100% - 75px);
	height: 20px;
	margin-left: 15px;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

.window-content {
	height: calc(100% - 40px);
	border: 1px solid #3b3b3b;
	border-bottom-left-radius: 2px;
	border-bottom-right-radius: 2px;
}
[contenteditable]:focus {
    outline: 0px solid transparent;
}

.terminal {
	margin: 5px;
	width: calc(100% - 10px);
	height: calc(100% - 10px);
	display: flex;
	justify-content: flex-end;
	flex-direction: column;
	white-space: pre;
}

.terminal-command {
	color: #f0f0f0;
}

.terminal-command-query {
	float: left;
	color: #8AE234;
}

.terminal-command-prompt {
	margin-left: 7px;
	background-color: inherit;
	outline: none;
	border: none;
	color: inherit;
	font: inherit;
	padding: 0px;
	width: calc(100% - 178px);
}	

.terminal-query-output {
	
}
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}	
